import React, { Component } from "react";
import { BrowserRouter, Route } from "react-router-dom";
import { connect } from "react-redux";
import Navbar from "./sidebar";
import PrivateRoute from "./PrivateRoute";
import Dashboard from "./dashboard";
import CalendarApp from "./calendar";
import { UserProfileApp } from "./user";
import Users from "./user";
import { loadUser, logout } from "../actions/accounts/auth";
import { Redirect, Link } from "react-router-dom";

import {
  Menu,
  Dropdown,
  Skeleton,
  Layout,
  Row,
  Col,
  Card,
  Icon,
  Button,
  Input
} from "antd";
const { Header, Footer, Sider, Content } = Layout;

class BaseLayout extends Component {
  render() {
    const menu = (
      <Menu>
        <Menu.Item key="0">
          <Link icon="user" to="/profile">
            <Button type="link" icon="user">
              Profile
            </Button>
          </Link>
        </Menu.Item>
        <Menu.Divider />
        <Menu.Item key="1">
          <Button type="link" onClick={this.props.logout} icon="poweroff">
            Logout
          </Button>
        </Menu.Item>
      </Menu>
    );
    return (
      <Layout style={{ minHeight: "100vh" }}>
        <Navbar />
        <Layout>
          <Header style={{ backgroundColor: "#fff" }}>
            <Row type="flex">
              <Col span={24}>
                <div className="align-right">
                  <Dropdown overlay={menu} trigger={["click"]}>
                    <a
                      style={{ fontWeight: "bold" }}
                      className="ant-dropdown-link"
                      href="#"
                    >
                      <Icon type="user" /> Hello,{" "}
                      {this.props.auth.user.username} <Icon type="down" />
                    </a>
                  </Dropdown>
                </div>
              </Col>
            </Row>
          </Header>
          <Content className="container">
            <BrowserRouter>
              <PrivateRoute exact path="/" component={Dashboard} />
              <PrivateRoute path="/profile" component={UserProfileApp} />
              <PrivateRoute path="/users" component={Users} />
              <PrivateRoute path="/calendar" component={CalendarApp} />
            </BrowserRouter>
          </Content>
          <Footer style={{ textAlign: "center" }}>Baylynmedia</Footer>
        </Layout>
      </Layout>
    );
  }
}

const mapStateToProps = state => ({ auth: state.auth });
export default connect(
  mapStateToProps,
  { logout, loadUser }
)(BaseLayout);
