import api from '../../apiurl'
import {
    USER_LOADED,
    USER_LOADING,
    AUTH_ERROR,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    LOGOUT_SUCCESS,
    REGISTER_SUCCESS,
    REGISTER_FAIL
  } from "../types";


// CHECK TOKEN & LOAD USER
export const loadUser = () => (dispatch, getState) => {
    // User Loading
    dispatch({ type: USER_LOADING });
    api.get("/api/v1/user_profile/", tokenConfig(getState))
    .then(res => {
        dispatch({
            type: USER_LOADED,
            payload: res.data[0]
        });
    })
    .catch(err => {
        //message.error(err.response.data.details,10);
        //dispatch(returnErrors(err.response.data, err.response.status));
        dispatch({
            type: AUTH_ERROR
        });
    });
};

// LOGIN USER
export const login = (username, password) => dispatch => {
    // Headers
    const config = {
      headers: {
        "Content-Type": "application/json"
      }
    };
  
    // Request Body
    const body = JSON.stringify({ username, password });
    dispatch({ type: USER_LOADING });

    api
      .post("/api/v1/login/", body, config)
      .then(res => {
        dispatch({
          type: LOGIN_SUCCESS,
          payload: res.data
        });
      })
      .catch(err => {
        //message.error(err.response.data.detail,10);
        //dispatch(returnErrors(err.response.data, err.response.status));
        dispatch({
          type: LOGIN_FAIL,
          payload:err.response.data.detail
        });

      });
  };
  

  // LOGOUT USER
export const logout = () => (dispatch, getState) => {
    api
      .post("/api/v1/logout/", null, tokenConfig(getState))
      .then(res => {
        dispatch({
          type: LOGOUT_SUCCESS
        });
      })
      .catch(err => {
        dispatch({
            type: AUTH_ERROR
        });
      });
  };


  // Setup config with token - helper function
export const tokenConfig = getState => {
    // Get token from state
    const token = getState().auth.token;
  
    // Headers
    const config = {
      headers: {
        "Content-Type": "application/json"
      }
    };
  
    // If token, add to headers config
    if (token) {
      config.headers["Authorization"] = `Bearer ${token}`;
    }
  
    return config;
};  