import {
  USER_LOADED,
  USER_LOADING,
  AUTH_ERROR,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT_SUCCESS
} from "../../actions/types";

const initialState = {
  token: null,//localStorage.getItem("token"),
  isAuthenticated: null,
  isLoading: false,
  isError:false,
  user: null
};

export default function(state = initialState, action) {
  switch (action.type) {
    case USER_LOADING:
      return {
        ...state,
        isLoading: true,
        isError:false
      };
    case USER_LOADED:
      return {
        ...state,
        isAuthenticated: true,
        isLoading: false,
        user: action.payload
      };
    case LOGIN_SUCCESS:
      //localStorage.setItem("token", action.payload.access);
      return {
        ...state,
        isAuthenticated: true,
        isLoading: false,
        token:action.payload.access
      };
    
    case LOGIN_FAIL:
      localStorage.removeItem("root");
      return {
        ...state,
        token: null,
        user: null,
        isAuthenticated: false,
        isLoading: false,
        isError : action.payload
      };
    case AUTH_ERROR:
    case LOGOUT_SUCCESS:
      localStorage.removeItem("root");
      return {
        ...state,
        token: null,
        user: null,
        isAuthenticated: false,
        isLoading: false,
        isError : false
      };
    default:
      return state;
  }
}